
import { defineComponent } from 'vue';

export default defineComponent({
    name: 'LoginIconview',
    data() {
        return {
            msg:"",
            ll1:"op02 llhide",
            ll2:"op02 llhide",
            ll3:"op02 llhide",
            ll4:"op02 llhide",
            ll5:"op02 llhide",
            ll6:"op02 llhide",
            ll7:"op02 llhide",
            loadingwait1:"op0",
            loadingwait2:"op0",
            loadingwait3:"op0"
        };
    },
    mounted(){
        var count = 0;
        var waitincount = 0;

        setInterval(() => {
            waitincount += 1;
            var waitingwhichphase = waitincount % 3;

            if(waitingwhichphase == 0){
                this.loadingwait2 = "op0";
                this.loadingwait3 = "op1";
            }
            if(waitingwhichphase == 1){
                this.loadingwait3 = "op0";
                this.loadingwait1 = "op1";
            }
            if(waitingwhichphase == 2){
                this.loadingwait1 = "op0";
                this.loadingwait2 = "op1";
            }
        }, 150);

        setInterval(() => {
            count += 1;
            var whichphase = count % 7;
            if(whichphase == 0){
                this.ll5 = "op0 llhide";
                this.ll6 = "op04 llhide";
                this.ll7 = "op1 llshow";
                this.ll1 = "op04 llhide";
            }
            if(whichphase == 1){
                this.ll7 = "op02 llhide";
                this.ll1 = "op1 llshow";
                this.ll2 = "op04 llhide";
            }
            if(whichphase == 2){
                this.ll1 = "op04 llhide";
                this.ll2 = "op1 llshow";
                this.ll3 = "op04 llhide";
            }
            if(whichphase == 3){
                this.ll1 = "op02 llhide";
                this.ll2 = "op04 llhide";
                this.ll3 = "op1 llshow";
                this.ll4 = "op04 llhide";
            }
            if(whichphase == 4){
                this.ll2 = "op02 llhide";
                this.ll3 = "op02 llhide";
                this.ll4 = "op1 llshow";
                this.ll5 = "op04 llhide";
            }
            if(whichphase == 5){
                this.ll3 = "op02 llhide";
                this.ll4 = "op04 llhide";
                this.ll5 = "op1 llshow";
                this.ll6 = "op04 llhide";
            }
            if(whichphase == 6){
                this.ll4 = "op02 llhide";
                this.ll5 = "op04 llhide";
                this.ll6 = "op1 llshow";
                this.ll7 = "op04 llhide";
            }
        }, 100);
        
    },
    methods: {
        LoadingMotion(){
            console.log("loaded!!");
        }
    }
});
