
import { defineComponent } from 'vue';
import {apiClient} from "@/http";

export default defineComponent({
    name: 'AccountModal',
    methods:{
      Logout(): Promise<any> {
        return apiClient.post(
        "/api/logout"
        )
        .then(() => {
            localStorage.setItem('access_token',"");
            this.$router.push('/login');
        })
        .catch(error => {
            console.log(error);
        });
      }
    }
});
